* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f8e9d6;
  font-family: VT323, monospace;
}

html, body {
  height: 100%;
  width: 100%;
}

button {
  text-transform: capitalize;
  border-radius: 0;
  margin: 2px 0;
  padding: 0 8px;
  font-family: inherit;
  font-size: 24px;
}

canvas {
  border: 1px solid;
}

.principal {
  width: max-content;
  margin: 64px auto;
  display: flex;
}

.painel {
  max-width: 230px;
  background-color: #c7cbce;
  background-image: url("bg.5b2d7409.png");
  border: 1px solid;
  margin: 0 8px;
  padding: 8px;
}

.elements {
  float: right;
  flex-wrap: wrap;
  gap: 0;
  display: flex;
}

.element-btn {
  opacity: .9;
  cursor: pointer;
  border-width: 3px;
  margin: 0;
  position: relative;
}

.element-btn:before {
  content: "";
  height: 100%;
  width: 100%;
  opacity: .1;
  background-image: url("button-bg.95e35696.png");
  position: absolute;
  top: 0;
  left: 0;
}

canvas, .painel {
  border-width: 4px;
}

canvas, .painel, .element-btn {
  border-style: outset;
  border-color: #969696;
  border-radius: 2px;
}

.element-selected {
  opacity: 1;
  border-style: dashed;
  border-color: #000;
}

.tools {
  float: left;
  background-color: #f0f8ff;
  flex-wrap: wrap;
  gap: 0;
  margin: 8px 0;
  display: flex;
}

.tool-btn {
  opacity: .6;
  cursor: pointer;
  margin: 0;
  position: relative;
}

.tool-selected {
  opacity: 1;
  border: 2px dashed #000;
}

.reset-btn {
  opacity: 1;
  background-color: #940101c7;
}

.hidden {
  display: none;
}

@media (max-width: 840px) {
  .painel {
    max-width: 100%;
    margin: 8px;
    position: fixed;
    bottom: 0;
    left: 0;
  }

  canvas {
    border: 0;
  }
}

